.container_item_detail{
    max-width: 1000px;
    margin-top: 80px;
    margin : 80px auto;
    background-color: rgb(232, 232, 232);
    padding: 30px 10px;
    border-radius: 10px;
}

.title_descripcion{
    font-size: 30px;
    text-align: center;
    margin-bottom: 40px;
    background-color: #ffffff;
    border-radius: 10px;
}


.imagen_container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    max-width: 500px;
    height: 100%;
    background-color: #f5f5f5;
    border-radius: 10px;
    overflow: hidden;
}


.title_article{
    font-size: 40px;
    font-weight: bold;
    color: #000000;
    margin-left: 20px;
    margin-top: 20px;
    text-align: center;
    margin: 0;
}

.discount{
    font-size: 25px;
    color: #000000;
    text-align: center;
    margin: 0;
    margin-left: 20px;
}

.past{
    font-size: 20px;
    color: #000000;
    text-align: center;
    margin: 0;
    margin-left: 20px;
}

.discount_price{
    font-size: 30px;
    font-weight: bold;
    color: #25b700;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
}

.past_price{
    font-size: 20px;
    font-weight: bold;
    color: #dc3545;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    text-decoration: line-through;
}



.item_decription{
    font-size: 20px;
    color: #000000;
    margin-left: 20px;
    margin-bottom: 20px;
    margin-top: 25px;
}

.codigo_article{
    font-size: 20px;
    color: #000000;
    margin-left: 20px;
    margin-bottom: 20px;
    text-align: center;
}

.codigo_number{
    font-size: 20px;
    color: #000000;
    margin-bottom: 20px;
    color: #5d0000;
}