.boton_ver_mas{
    background-color: #FFC300;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    display: block;
    width: 100%;
    text-align: center;
    margin-top: 5px;
    margin-bottom: 5px;
}

.boton_ver_mas:hover{
    background-color: #ffffff;
    color: #FFC300;
    border: 1px solid #FFC300;
}

