.title_editar{
    text-align: center;
    font-size: 40px;
    font-weight: 600;
    margin-bottom: 1rem;
    color: #fff;
    margin-top: 40px;
}

.form_edit{
    background-color: #fff;
    padding: 20px;
    margin: 40px auto;
    border-radius: 10px;
    max-width: 800px;
}

.boton_editar{
    background-color: rgb(17, 0, 100);
    color: rgb(255, 255, 255);
    border: 1px solid rgb(255, 255, 255);
    border-radius: 5px;
    padding: 3px 10px;
    margin: 10px auto;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
    width: 400px;

}